import PropTypes from 'prop-types';
import React, {useState} from 'react';

// material-ui
import { useTheme, makeStyles } from '@material-ui/styles';
import { Button, Grid, Typography, CardContent,Checkbox, CardActions, CardMedia, Divider, IconButton, Switch, FormControlLabel, List, ListItemIcon, ListItemText } from '@material-ui/core';
import ListItemButton from '@material-ui/core/ListItemButton';

// third party
import PerfectScrollbar from 'react-perfect-scrollbar';
import AccountCircleTwoTone from '@material-ui/icons/AccountCircleTwoTone';
import { gridSpacing } from 'store/constant';
import AnimateButton from 'ui-component/extended/AnimateButton';

import { useDispatch } from 'react-redux';

import { SNACKBAR_OPEN, LOGIN, ACCOUNT_INITIALIZE } from 'store/actions';

import useAuth from 'hooks/useAuth'
import ViewProductModal from './ViewProductModal';

// project imports
import MainCard from 'ui-component/cards/MainCard';
import RevenueCard from 'ui-component/cards/RevenueCard';
import Logo from 'ui-component/Logo';


// assets
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';

// style constant
const useStyles = makeStyles((theme) => ({
    incomingRequestsCard: {
        padding: '0px',
        paddingBottom: '0px !important'
    },
    textSuccess: {
        color: theme.palette.success.dark
    },
    textError: {
        color: theme.palette.error.main
    },
    ScrollHeight: {
        height: '370px',
        '& svg': {
            width: '32px',
            margin: '-6px 6px -6px -6px'
        }
    },
    coinText: {
        display: 'flex',
        justifyContent: 'space-between'
    }
}));

// ===========================|| DASHBOARD ANALYTICS - TOTAL REVENUE CARD ||=========================== //

const TotalRevenueCard = ({ title }) => {


    const dispatch = useDispatch()

    const {activeStore, activeStore2, activeStoreServices, refreshApprovalStore, url} = useAuth()
    const classes = useStyles();
    const theme = useTheme();

    console.log("STORE", activeStore)
    console.log("STORE2", activeStore2)
    console.log("Store Logo", activeStore2.store_logo)

const [modalProduct, setModalProduct] = React.useState('')
    const [open, setOpen] = React.useState(false);
        const handleClickOpenDialog = (product) => {
            setOpen(true);
            setModalProduct(product)
        };
        const handleCloseDialog = () => {
            setOpen(false);
        };

    const [productsActive, setProductsActive] = useState(true)
    const [servicesActive, setServicesActive] = useState(false)

    const chooseProducts = () => {
        setProductsActive(true)
        setServicesActive(false)
    }

    const chooseServices = () => {
        setProductsActive(false)
        setServicesActive(true)
    }


    let logo = ''
    try {
        logo = `${url}${activeStore2.store_logo}`
    }catch {
        console.log("Store Logo Not Cool")
    }
    console.log("LOGO", logo)

   
    const [aproval, setAproval] = React.useState(activeStore2.is_approved)
    const runAproval = (thebool) => {
        console.log(thebool)
        /* setAproval(thebool) */
    }

    const [invisible, setInvisible] = React.useState(activeStore2.is_approved);
    console.log("APPROVAL", activeStore2.is_approved)
    const handleAproval = async() => {
        if (activeStore2.is_approved) {
            activeStore2.is_approved = false
            var formdata = new FormData();
                formdata.append("is_approved", "False");

                var requestOptions = {
                  method: 'PATCH',
                  body: formdata,
                  redirect: 'follow'
                };

                refreshApprovalStore()

                const res = await fetch(`${url}/api/patch-store-approval/${activeStore2.id}/`, requestOptions)
                if (res.status === 200) {
                    console.log("Store DisApproved")
                    dispatch({
                        type: SNACKBAR_OPEN,
                        open: true,
                        message: 'Store DisApproved',
                        variant: 'alert',
                        alertSeverity: 'success'
                    });
                }else {
                    dispatch({
                        type: SNACKBAR_OPEN,
                        open: true,
                        message: 'Approval Process Failed',
                        variant: 'alert',
                        alertSeverity: 'error'
                    });
                }

        } else {
            activeStore2.is_approved = true
            var formdata = new FormData();
                formdata.append("is_approved", "True");

                var requestOptions = {
                  method: 'PATCH',
                  body: formdata,
                  redirect: 'follow'
                };

                const res = await fetch(`${url}/api/patch-store-approval/${activeStore2.id}/`, requestOptions)
                if (res.status === 200) {
                    console.log("Store Approved")
                    dispatch({
                        type: SNACKBAR_OPEN,
                        open: true,
                        message: 'Store Approved',
                        variant: 'alert',
                        alertSeverity: 'success'
                    });
                }else {
                    dispatch({
                        type: SNACKBAR_OPEN,
                        open: true,
                        message: 'Approval Process Failed',
                        variant: 'alert',
                        alertSeverity: 'error'
                    });
                }
        }
        
    };

    return (
        <MainCard /* title={title} */ content={false}> 
        <div style={{position: 'relative'}}>
            <CardMedia
                sx={{ height: 210 }}
                component="img"
                image={logo}
                /* image={require('assets/images/goodshop-01.png')} */
                title="Contemplative Reptile"
                  />
            <div style={{position: 'absolute', top: '0', left: '0', width: '100%', height: '30%', opacity: '0.95', backgroundColor: '#40ae49'}}>
                <Typography variant="h3" style={{marginTop: '20px', textAlign: 'center' ,color: '#f68e1f'}}>{activeStore2.name}</Typography>
            </div>
        </div>
            <CardActions>
                    <FormControlLabel
                        style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}
                        control={<Switch color="primary" checked={activeStore2.is_approved} onChange={handleAproval} />}
                        label={activeStore2.is_approved === true ? 'Disapprove Store':'Approve Store'} sx={{borderTop:'#40ae49 solid 4px', borderBottom:'#f68e1f solid 4px', marginLeft: '45px'}}
                        />
            </CardActions>
            <Grid item xs={12} md={12} style={{marginLeft: '45px'}}>
            <Grid container spacing={2}>
                        <Grid item xs={12} md={6} /* style={{marginLeft: '45px'}} */>
                            <FormControlLabel control={<Checkbox checked={productsActive} onClick={chooseProducts} name="products"/>} label="Products" />
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <FormControlLabel control={<Checkbox checked={servicesActive} onClick={chooseServices} name="services"/>} label="Services" />
                        </Grid>
                    </Grid>
                </Grid>
            <CardContent className={classes.incomingRequestsCard}>
                {/* <Grid item xs={12} lg={8}>
                    <Grid item xs={12} lg={6} md={6}>
                        <AnimateButton>
                            <Button type="submit"  variant="contained">Services</Button>
                        </AnimateButton>
                    </Grid>
                    <Grid item xs={12} lg={6} md={6}>
                        <AnimateButton>
                            <Button type="submit"variant="contained">Products</Button>
                        </AnimateButton>
                    </Grid>
                </Grid> */}
                <PerfectScrollbar className={classes.ScrollHeight}>
                    <List component="nav" aria-label="main mailbox folders">
                        {productsActive ? 
                            activeStore.map((product, index) =>(
                        <ListItemButton
                            onClick={()=> handleClickOpenDialog(product)}
                            >
                            <ListItemIcon>
                                <ArrowDropUpIcon className={classes.textSuccess} />
                            </ListItemIcon>
                            <ListItemText
                                primary={
                                    <div className={classes.coinText}>
                                        <span>{product.name}</span>
                                        <span className={classes.textSuccess}>${product.price}</span>
                                    </div>
                                }
                            />
                        </ListItemButton>))

                            : 
                            activeStoreServices.map((product, index) => (
                        <ListItemButton
                            onClick={()=> handleClickOpenDialog(product)}
                            >
                            <ListItemIcon>
                                <ArrowDropUpIcon className={classes.textSuccess} />
                            </ListItemIcon>
                            <ListItemText
                                primary={
                                    <div className={classes.coinText}>
                                        <span>{product.name}</span>
                                        <span className={classes.textSuccess}>${product.price}</span>
                                    </div>
                                }
                            />
                        </ListItemButton>))
                            }

                        
                        <Divider />
                        <ViewProductModal open={open} handleCloseDialog={handleCloseDialog} modalProduct={modalProduct}/>
          
                    </List> 
                </PerfectScrollbar>
            </CardContent>
        </MainCard>
    );
};

TotalRevenueCard.propTypes = {
    title: PropTypes.string
};

export default TotalRevenueCard;







