import React, {useEffect, useState} from 'react';

// material-ui
import { useTheme, makeStyles } from '@material-ui/styles';
import { Grid, Typography, useMediaQuery } from '@material-ui/core';

// project imports
import MarketShareAreaChartCard from './MarketShareAreaChartCard';
import TotalRevenueCard from './TotalRevenueCard';
import LatestCustomerTableCard from './LatestCustomerTableCard';
import MainCard from 'ui-component/cards/MainCard';
import RevenueCard from 'ui-component/cards/RevenueCard';
import DashboardStoreCard from 'ui-component/cards/DashboardStoreCard';
import UserCountCard from 'ui-component/cards/UserCountCard';
import { gridSpacing } from 'store/constant';

import useAuth from 'hooks/useAuth'

// assets
import { IconShare, IconAccessPoint, IconCircles, IconCreditCard } from '@tabler/icons';
import MonetizationOnTwoToneIcon from '@material-ui/icons/MonetizationOnTwoTone';
import AccountCircleTwoTone from '@material-ui/icons/AccountCircleTwoTone';
import DescriptionTwoToneIcon from '@material-ui/icons/DescriptionTwoTone';

// style constant
const useStyles = makeStyles((theme) => ({
    flatCardBody: {
        '& >div': {
            padding: '0px !important'
        },
        '& svg': {
            width: '50px',
            height: '50px',
            color: theme.palette.secondary.main,
            borderRadius: '14px',
            padding: '10px',
            backgroundColor: theme.palette.mode === 'dark' ? theme.palette.background.default : theme.palette.primary.light
        }
    },
    flatCardBlock: {
        padding: '20px',
        borderLeft: '1px solid ',
        borderBottom: '1px solid ',
        borderLeftColor: theme.palette.mode === 'dark' ? theme.palette.dark.main : theme.palette.grey[200],
        borderBottomColor: theme.palette.mode === 'dark' ? theme.palette.dark.main : theme.palette.grey[200]
    }
}));

// ===========================|| ANALYTICS DASHBOARD ||=========================== //

const Analytics = () => {
    const theme = useTheme();
    const classes = useStyles();
    const matchDownXs = useMediaQuery(theme.breakpoints.down('xs'));
    const { url } = useAuth()
    const [stores, setStores] = useState([])
    const [approved, setApproved] = useState(true)

    const getStores = async() => {
        var requestOptions = {
          method: 'GET',
          headers: {'Content-Type':'application/json'},
          redirect: 'follow'
        };
        const res = await fetch(`${url}/api/real-stores`, requestOptions)

            console.log('RES')
            console.log(res)
            const data = await res.json()

            console.log('DATA')
            console.log(data)
            setStores(data)
    }


    const getApprovedStores = () => {
        setApproved(false)
    }

    const getUnapprovedStores = () => {
        setApproved(true)
    }

    const logme = (id) => {
        console.log("hello", id)
    }

    let unapprovedCount = 0
    let approvedCount = 0

    stores.filter(store => store.is_approved === false).map(store => {
        unapprovedCount += 1
    })

    stores.filter(store => store.is_approved === true).map(store => {
        approvedCount += 1
    })
    


    useEffect(()=> {
        getStores()
    }, [approved])

    return (
        <Grid container spacing={gridSpacing}>
            <Grid item xs={12} lg={8} md={8}>
                <Grid container spacing={gridSpacing}>
                    <Grid item xs={12} lg={6} md={6} onClick={getApprovedStores}>
                        <UserCountCard
                            primary="Approved Stores"
                            secondary={approvedCount}
                            iconPrimary={AccountCircleTwoTone}
                            color={theme.palette.primary.main}
                        />
                    </Grid>
                    <Grid item xs={12} lg={6} md={6} onClick={getUnapprovedStores}>
                        <UserCountCard
                            primary="Unaproved Stores"
                            secondary={unapprovedCount}
                            iconPrimary={DescriptionTwoToneIcon}
                            color={theme.palette.primary.main}
                        />
                    </Grid>
                    {/* <Grid item xs={12}>
                        <MarketShareAreaChartCard />
                    </Grid> */}


                    {approved ?           

                    stores.filter(store => store.is_approved === false)
                        .map((store, index)=> {logme(store.id); return (
                    <Grid key={index} item xs={12} lg={6} md={6}>
                        <RevenueCard
                            id={store.id}
                            date_created ={store.date_created}
                            is_approved={store.is_approved}
                            primary={store.name}
                            secondary={store.products.length}
                            content={store.services.length}
                            iconPrimary={AccountCircleTwoTone}
                            color={theme.palette.secondary.main}
                        />
                    </Grid>
                    )})
                    :

                    stores.filter(store => store.is_approved === true)
                     .map((store, index)=>(
                    <Grid key={index} item xs={12} lg={6} md={6}>
                        <RevenueCard
                            id={store.id}
                            date_created ={store.date_created}
                            is_approved={store.is_approved}
                            primary={store.name}
                            secondary={store.products.length}
                            content={store.services.length}
                            iconPrimary={AccountCircleTwoTone}
                            color={theme.palette.secondary.main}
                        />
                    </Grid>
                    ))

                        }


                    




                    {/* <Grid item xs={12} lg={6} md={4}>
                        <DashboardStoreCard
                            primary="Orders Received"
                            secondary="486"
                            content="20% Increase"
                            iconPrimary={AccountCircleTwoTone}
                            color={theme.palette.primary.main}
                        />
                    </Grid> */}
                </Grid>
                    {/* <Grid item xs={12}>
                        <LatestCustomerTableCard title="Latest Customers" />
                    </Grid> */}
                </Grid>
            
            <Grid item xs={12} lg={4} md={4}>
                <Grid container spacing={gridSpacing}>
                    {/* <Grid item xs={12}>
                        <MainCard className={classes.flatCardBody}>
                            <Grid container alignItems="center" spacing={0}>
                                <Grid item xs={12} sm={6} className={classes.flatCardBlock}>
                                    <Grid
                                        container
                                        alignItems="center"
                                        spacing={1}
                                        justifyContent={matchDownXs ? 'space-between' : 'center'}
                                    >
                                        <Grid item>
                                            <IconShare stroke={1.5} />
                                        </Grid>
                                        <Grid item sm zeroMinWidth>
                                            <Typography variant="h5" align="center">
                                                1000
                                            </Typography>
                                            <Typography variant="subtitle2" align="center">
                                                SHARES
                                            </Typography>
                                        </Grid>
                                    </Grid>
                                </Grid>
                                <Grid item xs={12} sm={6} className={classes.flatCardBlock}>
                                    <Grid
                                        container
                                        alignItems="center"
                                        spacing={1}
                                        justifyContent={matchDownXs ? 'space-between' : 'center'}
                                    >
                                        <Grid item>
                                            <IconAccessPoint stroke={1.5} />
                                        </Grid>
                                        <Grid item sm zeroMinWidth>
                                            <Typography variant="h5" align="center">
                                                600
                                            </Typography>
                                            <Typography variant="subtitle2" align="center">
                                                NETWORK
                                            </Typography>
                                        </Grid>
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Grid container alignItems="center" spacing={0}>
                                <Grid item xs={12} sm={6} className={classes.flatCardBlock}>
                                    <Grid
                                        container
                                        alignItems="center"
                                        spacing={1}
                                        justifyContent={matchDownXs ? 'space-between' : 'center'}
                                    >
                                        <Grid item>
                                            <IconCircles stroke={1.5} />
                                        </Grid>
                                        <Grid item sm zeroMinWidth>
                                            <Typography variant="h5" align="center">
                                                3550
                                            </Typography>
                                            <Typography variant="subtitle2" align="center">
                                                RETURNS
                                            </Typography>
                                        </Grid>
                                    </Grid>
                                </Grid>
                                <Grid item xs={12} sm={6} className={classes.flatCardBlock}>
                                    <Grid
                                        container
                                        alignItems="center"
                                        spacing={1}
                                        justifyContent={matchDownXs ? 'space-between' : 'center'}
                                    >
                                        <Grid item>
                                            <IconCreditCard stroke={1.5} />
                                        </Grid>
                                        <Grid item sm zeroMinWidth>
                                            <Typography variant="h5" align="center">
                                                100%
                                            </Typography>
                                            <Typography variant="subtitle2" align="center">
                                                ORDER
                                            </Typography>
                                        </Grid>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </MainCard>
                    </Grid> */}
                    <Grid item xs={12}>
                        <TotalRevenueCard title="Total Revenue" />
                    </Grid>
                    {/* <Grid item xs={12}>
                        <UserCountCard
                            primary="Daily user"
                            secondary="1,658"
                            iconPrimary={AccountCircleTwoTone}
                            color={theme.palette.secondary.main}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <UserCountCard
                            primary="Daily page view"
                            secondary="1K"
                            iconPrimary={DescriptionTwoToneIcon}
                            color={theme.palette.primary.main}
                        />
                    </Grid> */}
                </Grid>
            </Grid>
        </Grid>
    );
};

export default Analytics;
